import Dragula, { Drake } from "dragula";
import { TinyEmitter } from "tiny-emitter";
import autoScroll from "dom-autoscroller";

type EventTypes = "cardMoved";

export interface Column {
    id: string;
    header?: string | Element;
}

export interface Card {
    id: string;
    content: string | Element;
}

interface InternalColumn extends Column {
    node: HTMLElement;
    cards: Array<InternalCard>;
}

interface InternalCard extends Card {
    node: HTMLElement;
}

export interface KanbanBoardOptions {
    /**
     * True if the board should be considered read-only and not allow any
     * changes initiated by the user.
     */
    readonly?: boolean;
}

export class KanbanBoard {
    private board: HTMLElement | null;
    private drake?: Drake;
    private emitter: TinyEmitter;
    private currentColumns: Array<InternalColumn> = [];

    /**
     * Creates a new kanban board instance that handles dragging cards around.
     * 
     * @param elementSelector The selector or element that will contain the board.
     * @param options The initializiation options for the board.
     */
    constructor(elementSelector: string | HTMLElement, options?: KanbanBoardOptions) {
        this.emitter = new TinyEmitter();

        if (elementSelector instanceof HTMLElement) {
            this.board = elementSelector;
        }
        else {
            this.board = document.querySelector(elementSelector);
        }

        if (this.board === null) {
            return;
        }

        while (this.board.firstChild !== null) {
            this.board.removeChild(this.board.firstChild);
        }

        this.board.classList.add("kanban-board");

        if (options?.readonly !== true) {
            this.setupDragula();
        }
    }

    /**
     * Configures the dragula instance to handle drag and drop.
     */
    private setupDragula() {
        this.drake = Dragula({
            revertOnSpill: true
        });

        this.drake.on("drop", this.onDrop.bind(this));
    }

    /**
     * Handles the completion of a drag operation ending with a drop to the new
     * location.
     * 
     * @param element The element being dropped.
     * @param target The container that the element was dropped on.
     * @param source The container that the element was dragged from.
     * @param sibling The first sibling after the element after it is dropped.
     */
    private onDrop(element: Element, target: Element, source: Element, sibling: Element) {
        if (!(element instanceof HTMLElement)) {
            return;
        }

        /* Get the source and destination column nodes. */
        const toColumnNode = <HTMLElement>target.closest(".kanban-column");
        const fromColumnNode = <HTMLElement>source.closest(".kanban-column");
        if (toColumnNode === null || fromColumnNode === null) {
            return;
        }

        /* Get the card and column identifiers. */
        const cardId = element.dataset.id;
        const toColumnId = toColumnNode.dataset.id;
        const fromColumnId = fromColumnNode.dataset.id;
        if (cardId === undefined || toColumnId === undefined || fromColumnId === undefined) {
            return;
        }

        /* Get the card and column objects. */
        const card = this.findCard(cardId);
        const toColumn = this.findColumn(toColumnId);
        const fromColumn = this.findColumn(fromColumnId);
        if (card === null || toColumn === null || fromColumn === null) {
            return;
        }

        /* Get the source and destination indexes for the card. */
        const fromColumnIndex = fromColumn.cards.indexOf(card);
        const toColumnIndex = Array.from(target.childNodes).indexOf(element);
        if (fromColumnIndex === -1 || toColumnIndex === -1) {
            return;
        }

        /* Remove the card from the old column and add to new. */
        fromColumn.cards.splice(fromColumnIndex, 1);
        toColumn.cards.splice(toColumnIndex, 0, card);

        const afterCardId = toColumnIndex >= 1 ? toColumn.cards[toColumnIndex - 1].id : null;

        this.emitter.emit("cardMoved", cardId, toColumnId, afterCardId, true);
    }

    protected findColumn(columnId: string) {
        const index = this.findColumnIndex(columnId);

        return index !== null ? this.currentColumns[index] : null;
    }

    protected findColumnIndex(columnId: string) {
        for (let i = 0; i < this.currentColumns.length; i++) {
            if (this.currentColumns[i].id === columnId) {
                return i;
            }
        }

        return null;
    }

    protected findColumnForCard(cardId: string) {
        for (let index in this.currentColumns) {
            const column = this.currentColumns[index];
            const cardIndex = this.findCardIndex(column, cardId);

            if (cardIndex !== null) {
                return column;
            }
        }

        return null;
    }

    protected findCard(cardId: string) {
        for (let index in this.currentColumns) {
            const column = this.currentColumns[index];
            const cardIndex = this.findCardIndex(column, cardId);

            if (cardIndex !== null) {
                return column.cards[cardIndex];
            }
        }

        return null;
    }

    protected findCardIndex(column: InternalColumn, cardId: string) {
        for (let i = 0; i < column.cards.length; i++) {
            if (column.cards[i].id === cardId) {
                return i;
            }
        }

        return null;
    }

    public addColumn(column: Column) {
        return this.insertColumnInternal(column, this.currentColumns.length);
    }

    public insertColumnBefore(column: Column, beforeColumnId: string) {
        const columnIndex = this.findColumnIndex(beforeColumnId);

        if (columnIndex === null) {
            return false;
        }

        return this.insertColumnInternal(column, columnIndex);
    }

    public getElementForColumn(columnId: string): HTMLElement | null {
        const column = this.findColumn(columnId);

        if (column === null) {
            return null;
        }

        return column.node;
    }

    /**
     * Gets the HTML element that corresponds to the given card identifier.
     * 
     * @param cardId The card identifier to find.
     * @returns The HTMLElement object or null if the card was not found.
     */
    public getElementForCard(cardId: string): HTMLElement | null {
        return this.findCard(cardId)?.node ?? null;
    }

    public getCardIdsForColumn(columnId: string): Array<string> {
        const column = this.findColumn(columnId);

        if (column === null) {
            return [];
        }

        return column.cards.map((c) => c.id);
    }

    /**
     * Inserts a new card in the column after the specified card.
     * 
     * @param card The card to be inserted.
     * @param columnId The column identifier that will contain the card.
     * @param afterCardId The identifier of the existing card that the new card will be inserted after.
     * @returns True if the card was inserted; otherwise false.
     */
    public insertCardAfter(card: Card, columnId: string, afterCardId: string | null): boolean {
        const column = this.findColumn(columnId);

        if (column === null) {
            return false;
        }

        if (afterCardId === null) {
            return this.insertCardInternal(column, card, 0);
        }
        else {
            const cardIndex = this.findCardIndex(column, afterCardId);

            if (cardIndex === null) {
                return false;
            }

            return this.insertCardInternal(column, card, cardIndex + 1);
        }
    }

    /**
     * Move a card to the specified location.
     * 
     * @param cardId The identifier of the card to be moved.
     * @param toColumnId The identifier of the column the card will be moved to.
     * @param afterCardId The identifier of the existing card the card should be placed after or null if it should be placed at the beginning.
     * @returns true if the move was successful; otherwise false.
     */
    public moveCard(cardId: string, toColumnId: string, afterCardId: string | null) {
        const fromColumn = this.findColumnForCard(cardId);
        const toColumn = this.findColumn(toColumnId);
        const card = this.findCard(cardId);

        if (toColumn === null || fromColumn === null || card === null) {
            return false;
        }

        const cardsContainer = toColumn.node.querySelector(".cards");
        if (cardsContainer === null) {
            return false;
        }

        const fromColumnCardIndex = fromColumn.cards.indexOf(card);
        if (fromColumnCardIndex === -1) {
            return false;
        }

        let insertAfterCard: InternalCard | null = null;
        if (afterCardId !== null) {
            const afterCard = this.findCard(afterCardId);

            if (afterCard === null) {
                return false;
            }

            insertAfterCard = afterCard;
        }

        // Check if the card is already in the expected place, such as when
        // we are being notified of a move we made ourselves.
        if (fromColumn.id === toColumn.id) {
            if (insertAfterCard === null && card.node.previousSibling === null) {
                console.log('card did not move');
                return true;
            }
            else if (insertAfterCard !== null && card.node.previousSibling === insertAfterCard.node) {
                console.log('card did not move');
                return true;
            }
        }

        card.node.remove();
        fromColumn.cards.splice(fromColumnCardIndex, 1);

        if (insertAfterCard !== null) {
            const index = toColumn.cards.indexOf(insertAfterCard);
            insertAfterCard.node.after(card.node);
            toColumn.cards.splice(index + 1, 0, card);
        }
        else {
            cardsContainer.prepend(card.node);
            toColumn.cards.splice(0, 0, card);
        }

        return true;
    }

    /**
     * Update the content for an existing card with the new HTML content.
     * 
     * @param cardId The identifier of the card to be updated.
     * @param content The new HTML content of the card.
     * @returns true if the card was found and updated; otherwise false.
     */
    public updateCardContent(cardId: string, content: string | Element): boolean {
        const card = this.findCard(cardId);

        if (card === null) {
            return false;
        }

        while (card.node.firstChild) {
            card.node.removeChild(card.node.firstChild);
        }

        if (content instanceof Element) {
            card.node.appendChild(content);
        }
        else if (typeof content === "string") {
            card.node.innerHTML = content;
        }

        return true;
    }

    /**
     * Remove a single card from the board.
     * 
     * @param cardId The identifier of the card to be removed.
     * @returns true if the card was found and removed; otherwise false.
     */
    public removeCard(cardId: string): boolean {
        const card = this.findCard(cardId);
        const column = this.findColumnForCard(cardId);

        if (card === null || column === null) {
            return false;
        }

        const cardIndex = this.findCardIndex(column, cardId);

        if (cardIndex === null) {
            return false;
        }

        card.node.remove();
        column.cards.splice(cardIndex, 1);

        return true;
    }

    /**
     * Add a new column to the board.
     * 
     * @param column The data that describes the new column.
     * @param columnIndex The position to insert the column at.
     * @returns true if the column was added; otherwise false.
     */
    protected insertColumnInternal(column: Column, columnIndex: number) {
        if (columnIndex > this.currentColumns.length) {
            return false;
        }

        const columnElement = document.createElement("div");
        columnElement.classList.add("kanban-column");
        columnElement.dataset.id = column.id;

        /* Create the element that will hold the title. */
        if (column.header !== undefined) {
            if (typeof column.header === "string") {
                columnElement.appendChild(new DOMParser().parseFromString(column.header, "text/html"));
            }
            else {
                columnElement.appendChild(column.header);
            }
        }
        else if (typeof column.header === "string") {
            const titleElement = document.createElement("div");
            titleElement.classList.add("title");
            titleElement.innerText = column.header;
            columnElement.appendChild(titleElement);
        }

        /* Create the scroll helper element. */
        const scrollElement = document.createElement("div");
        scrollElement.classList.add("cards-container");
        columnElement.appendChild(scrollElement);

        /* Create the element that will hold the cards. */
        const cardsElement = document.createElement("div");
        cardsElement.classList.add("cards");
        scrollElement.appendChild(cardsElement);

        if (columnIndex < this.currentColumns.length) {
            const beforeColumn = this.currentColumns[columnIndex];
            this.board!.insertBefore(columnElement, beforeColumn.node);
        }
        else {
            this.board!.appendChild(columnElement);
        }

        const internalColumn: InternalColumn = {
            ...column,
            node: columnElement,
            cards: []
        };
        this.currentColumns.splice(columnIndex, 0, internalColumn);

        /* Handle automatic scrolling when they are near the edge. */
        if (this.drake !== undefined) {
            const drake = this.drake;

            drake.containers.push(cardsElement);

            autoScroll([scrollElement], {
                margin: 30,
                maxSpeed: 10,
                autoScroll: function () {
                    return this.down && drake.dragging;
                }
            });
        }

        return true;
    }

    /**
     * Adds a new card to the specified column.
     * 
     * @param column The column to add the card to.
     * @param card The data that describes the new card.
     * @param cardIndex The position in the column to insert the card at.
     * @returns true if the card was added; otherwise false.
     */
    protected insertCardInternal(column: InternalColumn, card: Card, cardIndex: number) {
        const cardsContainer = column.node.querySelector(".cards");

        if (cardsContainer === null || cardIndex > column.cards.length) {
            return false;
        }

        /* Create the card element that will hold the user's content. */
        const cardElement = document.createElement("div");
        cardElement.classList.add("kanban-card");

        if (card.id !== undefined) {
            cardElement.dataset.id = card.id;
        }

        if (card.content instanceof Element) {
            cardElement.appendChild(card.content);
        }
        else if (typeof card.content === "string") {
            cardElement.innerHTML = card.content;
        }

        if (cardIndex < column.cards.length) {
            const beforeCard = column.cards[cardIndex];
            cardsContainer.insertBefore(cardElement, beforeCard.node);
        }
        else {
            cardsContainer.appendChild(cardElement);
        }

        const internalCard: InternalCard = {
            ...card,
            node: cardElement
        };

        column.cards.splice(cardIndex, 0, internalCard);

        return true;
    }

    /**
     * Adds a new event handler for the specified event.
     * 
     * @param event The event to be handled.
     * @param callback The function to call when the event happens.
     * @returns This instance so that you can chain events.
     */
    public on(event: "cardMoved", callback: (cardId: string, columnId: string, afterCardId: string | null, userInitiated: boolean) => void): KanbanBoard;

    /**
     * Adds a new event handler for the specified event.
     *
     * @param event The event to be handled.
     * @param callback The function to call when the event happens.
     * @returns This instance so that you can chain events.
     */
    public on(event: string, callback: Function) {
        this.emitter.on(event, callback);

        return this;
    }

    /**
     * Removes the event handler for the specified event.
     * 
     * @param event The event to be turned off.
     * @param callback The callback to turn off, or undefined to turn off all callbacks.
     * @returns This instance so that you can chain events.
     */
    public off(event: EventTypes, callback: Function | undefined) {
        this.emitter.off(event, callback);

        return this;
    }
}
