import { KanbanBoard } from "./kanban-board";
import { hubConnection, Proxy } from "signalr-no-jquery";
import { ProjectProxy, BoardData, CardData, ColumnData } from "./interfaces";
import tippy from "tippy.js";

import "./project-board.scss";
import "tippy.js/dist/tippy.css";
import "tippy.js/themes/light.css";

type AddProjectCallback = (columnId: string) => void;
type AddTaskCallback = (columnId: string) => void;

/**
 * The options to initialize the board with.
 */
export interface ProjectBoardOptions {
    /** The board identifier to be displayed. */
    boardId: string;

    /** The element to be used to initialize the board in. */
    elementSelector: string;

    /** The function to be called when the user wants to add a project. */
    onAddProject?: AddProjectCallback;

    /** The function to be called when the user wants to add a task. */
    onAddTask?: AddTaskCallback;
}

/**
 * A project board handles displaying all the project and task cards on a board.
 * This includes allowing the user to drag and drop cards as well as add new
 * projects or tasks.
 */
export class ProjectBoard {
    // #region Private Properties

    /** The options we were initialized with. */
    private options: ProjectBoardOptions;

    /** The HTML Element that contains all our elements. */
    private boardContainer: HTMLElement | null;

    /** The KanbanBoard that we created and is tracking the user interaction. */
    private kanban?: KanbanBoard;

    /** The SignalR proxy we use to communicate to the server with. */
    private proxy?: ProjectProxy;

    /** True if we are in an error state and cannot take new requests. */
    private hasError: boolean = false;

    /** True if the board is in read-only state and should not allow user changes. */
    private readonly: boolean = false;

    /** Values will be true if we are currently in the process of loading cards. */
    private loadingCardsForColumn: Record<string, boolean> = {};

    // #endregion

    // #region Constructors

    /**
     * Create a new instance of the project board and prepare it for use.
     * 
     * @param elementSelector The DOM selector for the element to initialize.
     * @param options The configuration options that define how this board works.
     */
    constructor(options?: ProjectBoardOptions) {
        if (options == undefined) {
            throw "Configuration options are missing.";
        }

        if (options.boardId === undefined) {
            throw "Board identifier is required.";
        }
        
        if (options.elementSelector === undefined || options.elementSelector === "") {
            throw "Element selector is required.";
        }

        this.options = options;
        this.boardContainer = document.querySelector(this.options.elementSelector);

        if (this.boardContainer === null) {
            throw "Element was not found to initialize board in.";
        }

        this.boardContainer.classList.add("project-board");

        /* Clear out any existing child elements. */
        while (this.boardContainer.firstChild !== null) {
            this.boardContainer.removeChild(this.boardContainer.firstChild);
        }

        const signalrConnection = hubConnection();
        this.proxy = this.getHubProxy(signalrConnection.createHubProxy('com.blueboxmoon.ProjectManagement.MessageHub'));

        this.proxy.on("boardUpdated", this.serverBoardUpdated.bind(this));
        this.proxy.on("cardMoved", this.serverCardMoved.bind(this));
        this.proxy.on("cardUpdated", this.serverCardUpdated.bind(this));
        this.proxy.on("cardRemoved", this.serverCardRemoved.bind(this));
        this.proxy.on("cardAdded", this.serverCardAdded.bind(this));

        signalrConnection.start().then(async () => {
            await this.setupInitialBoardAsync();
        });
    }

    // #endregion

    // #region Methods

    /**
     * Setup the initial board with data provided from the server over the
     * signalR connection.
     */
    private async setupInitialBoardAsync() {
        try {
            let board = await this.proxy!.server.getBoardDataAsync(this.options.boardId);

            if (board === null) {
                this.showError("Failed to get board data from the server.");
                return;
            }

            this.readonly = board.readonly;

            /* Create a new child element to contain the actual kanban board. */
            const kanbanElement = document.createElement("div");
            this.boardContainer!.appendChild(kanbanElement);

            this.kanban = new KanbanBoard(kanbanElement, {
                readonly: this.readonly
            });

            /* Create all the columns on the board. */
            for (let columnIndex in board.columns) {
                const column = board.columns[columnIndex];

                this.kanban.addColumn({
                    id: column.id,
                    header: this.getColumnHeader(column)
                });

                var columnDom = this.kanban.getElementForColumn(column.id)?.querySelector(".cards-container");
                if (columnDom != null && columnDom !== undefined) {
                    columnDom.addEventListener("scroll", () => this.onColumnScroll(column.id));
                }

                this.loadMoreCardsForColumn(column.id);
            }

            this.configureForFixedHeight();

            this.kanban.on("cardMoved", this.kanbanCardMoved.bind(this));
        }
        catch (ex) {
            console.log(ex);
            throw ex;
        }
    }

    /**
     * Called when a column has been scrolled by the user. This is used
     * for detection of infinite scroll data loading.
     * 
     * @param columnId The column identifer that was scrolled.
     */
    private onColumnScroll(columnId: string) {
        if (!this.isValid()) {
            return;
        }

        const columnElement = this.kanban!.getElementForColumn(columnId)?.querySelector(".cards-container");

        if (columnElement === null || columnElement === undefined) {
            return;
        }

        if (this.shouldLoadMoreCardsForScroll(columnId) === true) {
            this.loadMoreCardsForColumn(columnId);
        }
    }

    /**
     * Determines if more cards should be loaded automatically to keep ahead
     * of the user if they scroll down.

      * @param columnId The columne identifer to be checked.
     */
    private shouldLoadMoreCardsForScroll(columnId: string) {
        const columnElement = this.kanban!.getElementForColumn(columnId)?.querySelector(".cards-container");

        if (columnElement === null || columnElement === undefined) {
            return false;
        }

        const scrollTop = columnElement.scrollTop;
        const scrollHeight = columnElement.scrollHeight;
        const height = columnElement.getBoundingClientRect().height;

        return scrollHeight - scrollTop - height - 100 <= 0;
    }

    /**
     * Attempt to load more cards from the server for the specified column.
     * 
     * @param columnId The column identifier to load more cards for.
     */
    private async loadMoreCardsForColumn(columnId: string): Promise<void> {
        if (this.loadingCardsForColumn[columnId] === true) {
            return;
        }

        this.loadingCardsForColumn[columnId] = true;

        /* Don't load more than 10 chunks in a single scroll event. */
        for (let loopCount = 0; loopCount < 10; loopCount++) {
            try {
                const existingCards = this.kanban!.getCardIdsForColumn(columnId);
                let lastCardId = existingCards.length > 0 ? existingCards[existingCards.length - 1] : null;

                const url = `/api/BBM_ProjectManagement_Utility/LoadCards?columnId=${columnId}&lastCardId=${lastCardId ?? ''}&count=20`;
                const response = await fetch(url);

                if (!response.ok) {
                    this.syncError();
                    return;
                }

                const cards = <CardData[]>await response.json();

                if (cards === null) {
                    this.syncError();
                    return;
                }

                for (let cardIndex in cards) {
                    const card = cards[cardIndex];

                    this.kanban!.insertCardAfter({
                        id: card.id,
                        content: this.getElementForCard(card)
                    }, columnId, lastCardId);

                    lastCardId = card.id;
                }

                if (cards.length === 0) {
                    // Return instead of break so we don't re-enable and then
                    // download more later.
                    return;
                }

                // If we don't need to load more to fill the scroll buffer then stop.
                if (!this.shouldLoadMoreCardsForScroll(columnId)) {
                    break;
                }
            }
            catch {
                this.loadingCardsForColumn[columnId] = false;
                this.syncError();

                // Return instead of break so we don't re-enable and then
                // download more later.
                return;
            }
        }

        this.loadingCardsForColumn[columnId] = false;
    }

    /**
     * Get the header element for the defined column.
     * 
     * @param column The column that needs the header build.
     * @returns The HTML element that should be placed in the header position.
     */
    private getColumnHeader(column: ColumnData): Element {
        const header = document.createElement("div");
        header.classList.add("header");

        const title = document.createElement("span");
        title.classList.add("title");
        title.innerText = column.name;

        const menu = document.createElement("span");
        menu.classList.add("menu");
        menu.innerHTML = `<i class="fa fa-ellipsis-h"></i>`;

        const menuContent = document.createElement("div");
        menuContent.classList.add("list-group", "margin-b-none");

        const menuAddProject = document.createElement("a");
        menuAddProject.href = "#";
        menuAddProject.innerText = "Add Project...";
        menuAddProject.classList.add("list-group-item");

        const menuAddTask = document.createElement("a");
        menuAddTask.href = "#";
        menuAddTask.innerText = "Add Task...";
        menuAddTask.classList.add("list-group-item");

        menuContent.append(menuAddProject, menuAddTask);

        if (this.readonly) {
            menuAddProject.classList.add("disabled");
            menuAddTask.classList.add("disabled");
        }

        const menuTippy = tippy(menu, {
            interactive: true,
            trigger: "click",
            arrow: true,
            content: menuContent,
            theme: "light"
        });

        menuAddProject.addEventListener("click", ev => {
            ev.preventDefault();

            if (menuAddProject.classList.contains("disabled")) {
                return;
            }

            menuTippy.hide();

            if (this.options.onAddProject !== undefined) {
                this.options.onAddProject(column.id);
            }
        });

        menuAddTask.addEventListener("click", ev => {
            ev.preventDefault();

            if (menuAddProject.classList.contains("disabled")) {
                return;
            }

            menuTippy.hide();

            if (this.options.onAddTask !== undefined) {
                this.options.onAddTask(column.id);
            }
        });

        header.append(title, menu);

        return header;
    }

    /**
     * Get the element that will be make up the content of an individual card.
     * 
     * @param card The card to be displayed.
     * @returns An element that can be added to the DOM.
     */
    private getElementForCard(card: CardData): Element {
       
        const container = document.createElement("div");
        container.innerHTML = card.content;

        // Initialize Bootstrap tooltips.
        (<any>window[<any>"$"])(container.querySelectorAll("[data-toggle='tooltip']"))
            .tooltip({
                container: 'body',
                delay: {
                    show: 250,
                    hide: 100
                }
            });

        const menu = document.createElement("span");
        menu.classList.add("menu");
        menu.innerHTML = `<i class="fa fa-ellipsis-h"></i>`;

        const menuContent = document.createElement("div");
        menuContent.classList.add("list-group", "margin-b-none");

        const menuRemove = document.createElement("a");
        menuRemove.href = "#";
        menuRemove.innerText = "Remove...";
        menuRemove.classList.add("list-group-item");

        if (this.readonly) {
            menuRemove.classList.add("disabled");
        }

        menuContent.append(menuRemove);

        const menuTippy = tippy(menu, {
            interactive: true,
            appendTo(ref: Element) {
                return ref.closest(".kanban-column") ?? document.body;
            },
            trigger: "click",
            arrow: true,
            content: menuContent,
            theme: "light"
        });

        menuRemove.addEventListener("click", ev => {
            ev.preventDefault();

            if (menuRemove.classList.contains("disabled")) {
                return;
            }

            menuTippy.hide();

            bootbox.dialog({
                message: "Are you sure you want to delete this card?",
                buttons: {
                    ok: {
                        label: "OK",
                        className: "btn-primary",
                        callback: async () => {
                            try {
                                this.kanban!.removeCard(card.id);
                                const result = await this.proxy!.server.removeCardAsync(card.id);
                                if (!result) {
                                    this.syncError();
                                }
                            }
                            catch {
                                this.syncError();
                            }
                        }
                    },
                    cancel: {
                        label: "Cancel",
                        className: "btn-default"
                    }
                }
            });
        });

        const cardContent = container.querySelector(".card-content");
        if (cardContent !== null && cardContent.firstChild !== null) {
            cardContent.firstChild.before(menu);
        }
        else {
            container.append(menu);
        }

        return container;
    }

    /**
     * Get a proxy that we can use to talk to the Project Management system.
     * 
     * @param proxy The generic proxy for the server connection.
     * @returns A ProjectProxy that can be used to talk to the server.
     */
    private getHubProxy(proxy: Proxy): ProjectProxy {
        const hubProxy = <ProjectProxy>proxy;

        hubProxy.server = {
            getBoardDataAsync: function (boardId: string) {
                return <Promise<BoardData>>hubProxy.invoke("getBoardData", boardId);
            },

            moveCardAsync: function (cardId: string, columnId: string, afterCardId: string | null) {
                return <Promise<boolean>>hubProxy.invoke("moveCard", cardId, columnId, afterCardId);
            },

            removeCardAsync: function (cardId: string) {
                return <Promise<boolean>>hubProxy.invoke("removeCard", cardId);
            }
        };

        return hubProxy;
    }

    /**
     * Determines if this project board is in a valid state for futher
     * processing.
     * 
     * @returns True if this instance is valid; otherwise false.
     */
    private isValid() {
        return this.kanban !== undefined && this.proxy !== undefined && this.hasError === false;
    }

    /**
     * A synchronization error with the server occurred.
     */
    private syncError() {
        this.showError("Looks like we need to reload and get everything all synced up again.");
    }

    /**
     * Display an error message and require the user to reload the page.
     * 
     * @param message The message to be displayed.
     */
    private showError(message: string) {
        this.hasError = true;

        const backdropNode = document.createElement("div");
        backdropNode.classList.add("alert-backdrop");
        this.boardContainer?.appendChild(backdropNode);

        const alertNode = document.createElement("div");
        alertNode.classList.add("alert-message");
        backdropNode.appendChild(alertNode);

        const contentNode = document.createElement("div");
        contentNode.classList.add("alert-content");
        alertNode.appendChild(contentNode);

        const alertTitleNode = document.createElement("div");
        alertTitleNode.classList.add("alert-title");
        alertTitleNode.innerText = "Error";
        contentNode.appendChild(alertTitleNode);

        const alertBodyNode = document.createElement("div");
        alertBodyNode.classList.add("alert-body");
        contentNode.appendChild(alertBodyNode);

        const alertTextNode = document.createElement("p");
        alertTextNode.innerText = message;
        alertBodyNode.appendChild(alertTextNode);

        const buttonContainerNode = document.createElement("div");
        buttonContainerNode.classList.add("text-center");
        alertBodyNode.appendChild(buttonContainerNode);

        const reloadButtonNode = document.createElement("span");
        reloadButtonNode.classList.add("btn", "btn-primary");
        reloadButtonNode.innerText = "Reload";
        buttonContainerNode.appendChild(reloadButtonNode);

        reloadButtonNode.addEventListener("click", () => {
            window.location.reload();
        });
    }

    /**
     * Resize the project board to fill the available height of the page.
     */
    private fixedHeightResize() {
        const mainFooter = document.querySelector(".main-footer");
        const mainFooterHeight = mainFooter !== null ? mainFooter.getBoundingClientRect().height : 0;
        const board = this.boardContainer!.querySelector(".kanban-board")! as HTMLElement;
        const clientHeight = document.documentElement.clientHeight;

        board.style.height = (clientHeight - board.getBoundingClientRect().top - mainFooterHeight - 5) + "px";
    }

    /**
     * Configures the project board and other elements on the page so that it
     * can operate in fixed height mode where the page doesn't scroll but the
     * individual columns do.
     */
    private configureForFixedHeight() {
        const mainContent = <HTMLElement>document.querySelector("#page-content");

        if (mainContent !== null) {
            mainContent.style.minHeight = "auto";
        }

        window.addEventListener("resize", this.fixedHeightResize.bind(this));

        /* 
         * Check status of the page load and either resize now or wait
         * for the page to finish loading before doing a resize.
         */
        if (/complete|interactive|loaded/.test(document.readyState)) {
            this.fixedHeightResize();
        } else {
            window.addEventListener("DOMContentLoaded", this.fixedHeightResize.bind(this));
        }
    }

    // #endregion

    // #region Event Handlers

    /**
     * One of the cards has moved inside the Kanban board. If this was a user
     * initiated action then we need to pass along the change to the server.
     * 
     * @param cardId The identifier of the card that was moved.
     * @param columnId The identifier of the column that the card was moved to.
     * @param afterCardId The identifier of the card that preceeds this card, or null if this is the first card in the column now.
     * @param userInitiated True if the user initiated this action; otherwise false.
     */
    private async kanbanCardMoved(cardId: string, columnId: string, afterCardId: string | null, userInitiated: boolean) {
        if (!this.isValid()) {
            return;
        }

        if (userInitiated) {
            try {
                const status = await this.proxy!.server.moveCardAsync(cardId, columnId, afterCardId);

                if (status === false) {
                    this.syncError();
                }
            }
            catch {
                this.syncError();
            }
        }
    }

    /**
     * The server has notified us that the board has been updated. Since this
     * is fairly rare and who knows exactly what happened to the board, just
     * force a reload situation.
     * 
     * @param boardId The board identifier that is being modified.
     */
    private serverBoardUpdated(boardId: string): void {
        if (!this.isValid() || this.options.boardId !== boardId) {
            return;
        }

        this.syncError();
    }

    /**
     * The server has notified us that a card has moved from one column to another.
     * 
     * @param boardId The board identifier that is being modified.
     * @param cardId The card identifier that is being moved.
     * @param toColumnGuid The column to which the card is being moved to.
     * @param afterCardGuid The card that the card is being placed after or null if it should be placed at the beginning.
     */
    private serverCardMoved(boardId: string, cardId: string, toColumnGuid: string, afterCardGuid: string | null): void {
        if (!this.isValid() || this.options.boardId !== boardId) {
            return;
        }

        if (this.kanban!.getElementForColumn(toColumnGuid) === null) {
            return this.syncError();
        }

        this.kanban!.moveCard(cardId, toColumnGuid, afterCardGuid);
    }

    /**
     * The server has notified us that a card has had it's content updated.
     * 
     * @param boardId The board identifier that is being modified.
     * @param card The card that was updated.
     */
    private serverCardUpdated(boardId: string, card: CardData) {
        if (!this.isValid() || this.options.boardId !== boardId) {
            return;
        }

        const content = this.getElementForCard(card);
        this.kanban!.updateCardContent(card.id, content);
    }

    /**
     * The server has notified us that a card has been removed from the system.
     * 
     * @param boardId The board identifier that is being modified.
     * @param cardId The identifier of the card that was removed.
     */
    private serverCardRemoved(boardId: string, cardId: string) {
        if (!this.isValid() || this.options.boardId !== boardId) {
            return;
        }

        this.kanban!.removeCard(cardId);
    }

    /**
     * A card has been added by way of something that happened on the server.
     * 
     * @param boardId The board identifier that is being modified.
     * @param card The card that was added to the board.
     * @param toColumnId The column the card was added it.
     * @param afterCardId The card that preceeds the new card.
     */
    private serverCardAdded(boardId: string, card: CardData, toColumnId: string, afterCardId: string | null) {
        if (!this.isValid() || this.options.boardId !== boardId) {
            return;
        }

        this.kanban!.insertCardAfter({
            id: card.id,
            content: this.getElementForCard(card)
        }, toColumnId, afterCardId);
    }

    // #endregion
}
